<template>
  <div>
    <v-card elevation="2" outlined :loading="cardLoading">
      <v-card-text>
        <v-row dense class="mb-n2">
          <v-col cols="12" class="text-left">
            <text-field-display :value="displayName"></text-field-display>
          </v-col>
        </v-row>
        <v-row dense class="mb-n2">
          <v-col cols="6" class="text-left justify-flex-start app-status-chip-wrapper">
            <app-status-chip :state="state"></app-status-chip>
          </v-col>
          <v-col cols="6" class="app-detail-switch-wrapper">
            <div class="mr-n3">
              <span class="grey--text caption">Enable</span>
              <v-switch
                @change="handleControlAction(!isEnabled ? toggleConfig.disable : toggleConfig.enable)"
                v-model="isEnabled"
                inset
                :disabled="cardLoading"
                color="success"
              ></v-switch>
            </div>
          </v-col>
        </v-row>
        <v-row dense>
          <v-col cols="5" class="text-left">
            <text-field-display label="Service Name" :value="serviceName"></text-field-display>
          </v-col>
          <v-col cols="3" class="text-left">
            <text-field-display label="Version" :value="version"></text-field-display>
          </v-col>
          <v-col cols="4" class="text-right">
            <text-field-display label="Active" :value="activeSince"></text-field-display>
          </v-col>
        </v-row>
      </v-card-text>
      <div v-if="appDetail.service" class="mb-3">
        <span class="flex-box justify-space-between px-2">
          <icon-button
            :key="buttonConfig.name"
            v-for="buttonConfig in actionButtons"
            :on-click="() => handleControlAction(buttonConfig.action)"
            :button-title="buttonConfig.title"
            :icon-key="buttonConfig.iconKey"
            :color="buttonConfig.color"
          ></icon-button>
        </span>
      </div>
    </v-card>
    <confirm-dialog
      :is-open="dialog.stop"
      :loading="cardLoading"
      :on-close="() => closeDialog(appActions.STOP)"
      :on-confirm="() => handleDialogConfirm(appActions.STOP)"
      dialog-text="Do you want to stop the app?"
    ></confirm-dialog>
    <confirm-dialog
      :is-open="dialog.restart"
      :loading="cardLoading"
      :on-close="() => closeDialog(appActions.RESTART)"
      :on-confirm="() => handleDialogConfirm(appActions.RESTART)"
      dialog-text="Do you want to restart the app?"
    ></confirm-dialog>
  </div>
</template>
<script>
import IconButton from './IconButton';
import ConfirmDialog from '../Common/ConfirmDialog';
import AppStatusChip from './AppStatusChip';
import TextFieldDisplay from '../Common/TextFieldDisplay';
import { RUBIX_APP_STATUS_CONTROL_ACTIONS } from '../../constants/appConstant';
import { CHANGE_SERVICE_STATUS } from '../../constants/messageConstants';
export default {
  name: 'app-details-view',
  props: ['app-detail', 'sync-app-details', 'change-service-status'],
  components: { AppStatusChip, IconButton, ConfirmDialog, TextFieldDisplay },
  data() {
    const btnNames = {
      startBtn: 'startBtn',
      restartBtn: 'restartBtn',
      stopBtn: 'stopBtn',
      enableBtn: 'enableBtn',
      disableBtn: 'disableBtn',
    };
    const btnConfig = {
      [btnNames.startBtn]: {
        name: btnNames.startBtn,
        title: 'Start',
        color: 'blue darken-2',
        iconKey: 'mdi-play-circle-outline',
        action: RUBIX_APP_STATUS_CONTROL_ACTIONS.START,
      },
      [btnNames.restartBtn]: {
        name: btnNames.restartBtn,
        title: 'Restart',
        color: 'green darken-2',
        iconKey: 'mdi-restart',
        action: RUBIX_APP_STATUS_CONTROL_ACTIONS.RESTART,
      },
      [btnNames.stopBtn]: {
        name: btnNames.stopBtn,
        title: 'Stop',
        onClick: this.onStart,
        iconKey: 'mdi-stop-circle-outline',
        action: RUBIX_APP_STATUS_CONTROL_ACTIONS.STOP,
      },
    };
    const toggleConfig = {
      enable: RUBIX_APP_STATUS_CONTROL_ACTIONS.ENABLE,
      disable: RUBIX_APP_STATUS_CONTROL_ACTIONS.DISABLE,
    };
    const actionBtnKeys = [btnNames.startBtn, btnNames.stopBtn, btnNames.restartBtn];
    return {
      appActions: { ...RUBIX_APP_STATUS_CONTROL_ACTIONS },
      cardLoading: false,
      dialog: {
        [RUBIX_APP_STATUS_CONTROL_ACTIONS.STOP]: false,
        [RUBIX_APP_STATUS_CONTROL_ACTIONS.RESTART]: false,
      },
      btnNames: btnNames,
      toggleConfig: toggleConfig,
      status: this.appDetail.status,
      isEnabled: this.appDetail.is_enabled,
      state: this.appDetail.state,
      version: this.appDetail.version || '-',
      serviceName: this.appDetail.service || '-',
      message: this.appDetail.msg || '',
      displayName: this.appDetail.display_name,
      activeSince: this.appDetail.time_since || 'N/A',
      actionButtons: actionBtnKeys.map(key => btnConfig[key]),
    };
  },
  methods: {
    handleControlAction(action) {
      switch (action) {
        case RUBIX_APP_STATUS_CONTROL_ACTIONS.START:
          this.updateServiceStatus(action);
          break;
        case RUBIX_APP_STATUS_CONTROL_ACTIONS.ENABLE:
          this.updateServiceStatus(action);
          break;
        case RUBIX_APP_STATUS_CONTROL_ACTIONS.DISABLE:
          this.updateServiceStatus(action);
          break;
        case RUBIX_APP_STATUS_CONTROL_ACTIONS.STOP:
          this.dialog[action] = true;
          break;
        case RUBIX_APP_STATUS_CONTROL_ACTIONS.RESTART:
          this.dialog[action] = true;
          break;
      }
    },
    closeDialog(action) {
      this.dialog[action] = false;
    },
    handleDialogConfirm(action) {
      this.updateServiceStatus(action, () => this.closeDialog(action));
    },
    updateServiceStatus(action, callback) {
      this.cardLoading = true;
      const isEnableDisableAction = action === this.toggleConfig.enable || action === this.toggleConfig.disable;

      this.changeServiceStatus(action, this.appDetail.service)
        .then(res => {
          if (!isEnableDisableAction) {
            typeof this.syncAppDetails === 'function' && this.syncAppDetails();
          }
          this.$toasted.show(CHANGE_SERVICE_STATUS.success);
        })
        .catch(err => {
          console.error(err);
          this.$toasted.show(CHANGE_SERVICE_STATUS.error, { type: 'error' });
        })
        .finally(() => {
          this.cardLoading = false;
          if (isEnableDisableAction) {
            this.$store.commit('changeAppDetailsStatus', {
              service: this.appDetail.service,
              isEnabled: this.isEnabled,
            });
          }
          if (typeof callback === 'function') callback();
        });
    },
  },
};
</script>
<style>
.flex-box {
  display: flex;
}
.app-status-chip-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.app-detail-switch-wrapper {
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.app-detail-switch-wrapper .caption {
  position: absolute;
  top: 0;
  right: 6px;
}
</style>
