<template>
  <div>
    <v-form ref="form" lazy-validation>
      <v-switch v-model="formData.isDhcp" label="DHCP?"></v-switch>

      <v-row>
        <v-col>
          <v-text-field v-model="formData.interface" label="Interface" required></v-text-field>
        </v-col>
        <v-col>
          <v-switch v-model="formData.network_reset" label="Reset Network?" :rules="[rules.required]"></v-switch>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-text-field v-if="!formData.isDhcp" v-model="formData.ip_address" label="IP Address" required :rules="[rules.required]"></v-text-field>
        </v-col>
        <v-col>
          <v-text-field v-if="!formData.isDhcp" v-model="formData.routers" label="Routers" required :rules="[rules.required]"></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-text-field v-if="!formData.isDhcp" v-model="formData.domain_name_server" label="Domain Name" required :rules="[rules.required]"></v-text-field>
        </v-col>
        <v-col>
          <v-text-field v-if="!formData.isDhcp" v-model="formData.netmask" label="Netmask" required :rules="[rules.required]"></v-text-field>
        </v-col>
      </v-row>
      <v-card-actions>
        <v-btn @click="cancel" color="darken-1" text> Cancel </v-btn>
        <v-btn @click="confirmDialog = true" color="blue darken-1" text> Save </v-btn>
      </v-card-actions>
    </v-form>
    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">
          Confirm?
        </v-card-title>
        <v-card-text>Are you sure?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="confirmDialog = false" >
            No
          </v-btn>
          <v-btn color="red darken-1" text @click="onConfirm">
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  export default {
    props: ['item', 'cancel', 'save'],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      confirmDialog: false
    }),
    computed: {
      formData () {
        return this.item;
      },
    },
    methods: {
      onConfirm() {
        this.confirmDialog = false;
        this.save(this.formData);
      }
    }
  };
</script>
