<template>
  <div>
    <v-tabs v-model="tab" fixed-tabs dark>
      <v-tab :key="0"> Installed Apps </v-tab>
      <v-tab :key="1"> Services </v-tab>
      <v-tab :key="2"> Networking </v-tab>
      <v-tab :key="3"> Host Info </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <installed-services></installed-services>
      </v-tab-item>
      <v-tab-item :key="1">
        <system-default-services></system-default-services>
      </v-tab-item>
      <v-tab-item :key="2">
        <networking></networking>
      </v-tab-item>
      <v-tab-item :key="3">
        <host-info></host-info>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import SystemDefaultServices from './SystemDefaultServices';
import InstalledServices from './InstalledServices';
import Networking from './Networking';
import HostInfo from './HostInfo';

export default {
  name: 'services',
  components: {
    SystemDefaultServices,
    Networking,
    InstalledServices,
    HostInfo,
  },
  data: () => ({
    tab: 0,
    tabs: ['installed-services', 'items', 'networking', 'host-info'],
    isDisabled: false,
  }),
  mounted() {
    this.setTab();
  },
  watch: {
    '$route.query.tab': function (val) {
      this.tab = this.tabs.indexOf(val);
    },
    'tab': function (val, oldVal) {
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'services', query: { tab: tabKey } });
      }
    },
  },
  mounted() {
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
  },
  methods: {
    setTab() {
      const { tab: tabKey } = this.$route.query;
      this.tab = tabKey ? this.tabs.indexOf(tabKey) : 0;
    },
  },
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
};
</script>

