<template>
  <div>
    <div v-if="state">
      <span class="font-weight-medium">
        <v-chip outlined :color="currentStatus.color">{{ currentStatus.displayName }}</v-chip>
      </span>
    </div>
    <div v-else>
      <span class="font-weight-medium">
        <v-chip outlined color="yellow">{{ defaultStatus }}</v-chip>
      </span>
    </div>
  </div>
</template>
<script>
const appStatusStates = {
  active: {
    name: 'active',
    displayName: 'Running',
    color: 'success',
  },
  reloading: {
    name: 'reloading',
    displayName: 'Reloading',
    color: 'yellow',
  },
  failed: {
    name: 'failed',
    displayName: 'Failed',
    color: 'red',
  },
  activating: {
    name: 'activating',
    displayName: 'Activating',
    color: 'yellow',
  },
  deactivating: {
    name: 'deactivating',
    displayName: 'Deactivating',
    color: 'yellow',
  },
  maintenance: {
    name: 'maintenance',
    displayName: 'Maintenance',
    color: 'yellow',
  },
};
export default {
  name: 'app-status-chip',
  props: ['status', 'state'],
  computed: {
    currentStatus() {
      const appStatus = appStatusStates[this.state];
      if (!appStatus) {
        return {
          name: 'default',
          displayName: this.capitalize(this.state),
          color: 'yellow',
        };
      }
      return appStatus;
    },
    defaultStatus() {
      return this.capitalize(this.state);
    },
  },
  methods: {
    capitalize(str) {
      if (!str) {
        str = '';
      }
      str = String(str);
      return str.charAt(0).toUpperCase() + str.slice(1);
    },
  },
};
</script>
