<template>
  <v-card-text>
    <page-title title="Installed Apps"></page-title>
    <v-spacer></v-spacer>
    <v-container fluid>
      <v-row no-gutter>
        <v-col cols="12" lg="4" md="6" sm="12" xs="12" v-for="appDetail in services" :key="appDetail.service">
          <app-details-view
            :key="`${recompute}-${appDetail.service}`"
            :app-detail="appDetail"
            :sync-app-details="fetchAllServices"
            :change-service-status="changeServiceStatus"
          ></app-details-view>
        </v-col>
      </v-row>
    </v-container>
  </v-card-text>
</template>

<script>
import logo from '../../assets/nube-frog-green.png';
import AppDetailsView from './AppDetailsView';
import * as appsService from '../../services/appsService';
import PageTitle from '../Common/PageTitle.vue';

export default {
  components: { AppDetailsView, PageTitle },
  data: () => ({
    logo,
    action: '',
    valid: false,
    isLoadingData: false,
    recompute: 0,
  }),
  computed: {
    services() {
      return this.$store.state.installedServices;
    },
  },
  mounted() {
    this.fetchAllServices();
  },
  methods: {
    fetchAllServices() {
      this.isLoadingData = true;
      return appsService
        .fetchAllInstalledApps()
        .then(r => {
          this.$store.commit('changeInstalledServices', r);
          this.recompute++;
        })
        .catch(err => {
          console.error(err);
        })
        .finally(() => {
          this.isLoadingData = false;
        });
    },
    changeServiceStatus: appsService.changeServiceStatus,
  },
};
</script>
<style scoped>
.value-style {
  font-weight: bold;
}
</style>

