import http from '../axios';

const endpoint = '/system-service';

export const fetchSystemServices = () => {
  return http.get(endpoint);
};

export const changeServiceStatus = (action, serviceName) => {
  return http.post(`${endpoint}/control`, [{ action, service: serviceName }]);
};

export const fetchSystemStatByServiceName = serviceName => {
  return http.get(`${endpoint}/${serviceName}`);
};
