<template>

  <div>
    <H3 align="left"> Network Inferface </H3>

    <v-data-table
      :headers="headers"
      :items="data"
      class="elevation-1"
      item-key="uuid"
      show-select
      disable-sort>
      <template v-slot:no-data> No data </template>
      <template v-slot:item.tools="{item}">
        <v-btn icon small class="white--text">
          <v-icon @click="() => onClickEdit(item)" small dark >
            mdi-square-edit-outline
          </v-icon>
        </v-btn>
      </template>
    </v-data-table>
     <H3 align="left"> Firewall </H3>
    <v-row style="margin-bottom: 20px">
      <v-col>
        <v-card elevation="2" outlined tile class="card-height">
          <v-card-title>
            <h5>Firewall</h5>
            <v-row align="center" justify="end">
              <v-avatar size="35">
                <img alt="logo" :src="logo" />
              </v-avatar>
            </v-row>
          </v-card-title>
          <v-card-text class="card-text-height text-left">
            <div>
              <span class="value-style">Firewall status: {{fireWallStatus}}</span>
            </div>
            <div>
              <span class="value-style"><v-switch v-model="fireWallStatus" @change="enableFirewall" label="Enable Firewall"></v-switch></span>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col>
        <v-card elevation="2" outlined tile class="card-height">
          <v-card-title>
            <h5>Rule list</h5>
            <v-row align="center" justify="end">
              <v-avatar size="35">
                <img alt="logo" :src="logo" />
              </v-avatar>
            </v-row>
          </v-card-title>
          <v-card-text class="card-text-height">
            <div class="text-left" v-for="rule of portRules">
              <span class="value-style">{{rule.index}} | {{rule.port}} | {{rule.action}}</span>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="dialog" max-width="400px">
      <v-card>
        <v-card-title>
          <span class="headline">Update</span>
        </v-card-title>
        <v-card-text>
          <update-networking :save="onSave" :cancel="onCancelUpdate" :item="currentItem"></update-networking>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

import axios from "../../axios";
import UpdateNetworking from './UpdateNetworking';
import logo from '../../assets/nube-frog-green.png';

export default {
  name: "networking",
  components: {
    UpdateNetworking
  },
  data:()=>({
    logo,
    dialog: false,
    portRules: [],
    fireWallStatus: false,
    headers: [],
    currentItem: {},
    data: []
  }),
  mounted(){
    this.getData();
    this.getFirewallData();
    this.getPortRules();
  },
  methods: {
    getPortRules() {
      axios.get(`rubix-service/networking/ufw/rules`).then(data => {
        this.portRules = data;
      });
    },
   async enableFirewall(v) {
    const enable = v ? 'enable': 'disable';
     await axios.post('rubix-service/networking/ufw/enable', { enable }).then(result => {
        this.$toasted.show(result.toString());
        this.getFirewallData();
      });
    },
    getFirewallData() {
      axios.get(`rubix-service/networking/ufw/status`).then(data => {
        this.fireWallStatus = data;
      });
    },
    onCancelUpdate() {
      this.dialog = false;
    },
    async onSave(data) {
      let postData;
      if (data.isDhcp) {
        postData = {
          interface: data.interface,
          network_reset: !!data.network_reset
        }
      } else {
        postData = {
          interface: data.interface,
          network_reset: !!data.network_reset,
          ip_address: data.ip_address,
          routers: data.routers,
          domain_name_server: data.domain_name_server,
          netmask: data.netmask
        }
      }
      axios.post(`rubix-service/networking/${data.isDhcp ? 'dhcp': 'static'}`, postData).then(r => {
        this.$toasted.show(`Success!`);
        this.dialog = false;
        this.getData();
      }).catch(({message}) => {
        this.$toasted.show(message, {type: 'error'});
      });
    },
    onClickEdit(item) {
      this.dialog = true;
      this.currentItem = {...item};
    },
    async getData() {
      const items = await axios.get('rubix-service/networking');
      const data = [];
      const headers = [{text: '', value: 'name'}];
      for (const key in items) {
        data.push({...items[key], name: key});
      }
      const fields = items[Object.keys(items)[0]];
      for (const key in fields) {
        headers.push({text: key, value: key})
      }
      headers.push({ text: 'Tools', value: 'tools' });
      this.headers = headers;
      this.data = data;
    }
  }
};
</script>
<style scoped>
  .value-style {
    font-weight: bold;
  }
</style>
